.photos {
  display: flex;
  justify-content: space-between;

  &__img {
    width: calc(50% - 10px);
    object-fit: cover;
    object-position: center;
    height: 260px;
    border-radius: 8px;

    &:hover,
    &:focus {
      box-shadow: 0 0 22px rgba(0, 83, 181, 0.5);
      outline: none;
    }
  }

  &__wide-left {
    .photos__img {
      &:first-child {
        width: calc(67% - 10px);
      }

      &:last-child {
        width: calc(33% - 10px);
      }
    }
  }

  &__wide-right {
    .photos__img {
      &:first-child {
        width: calc(33% - 10px);
      }

      &:last-child {
        width: calc(67% - 10px);
      }
    }
  }
}
